"use client";

import { useState, useEffect } from "react";
import Link from "next/link";
import { ChevronLeft, Upload, Loader2 } from "lucide-react";
import { Badge } from "@/components/ui/badge";
import { Button } from "@/components/ui/button";
import {
  Card,
  CardContent,
  CardDescription,
  CardHeader,
  CardTitle,
} from "@/components/ui/card";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Textarea } from "@/components/ui/textarea";
import { Ebook, ebooks } from "@/lib/data";
import { useToast } from "@/hooks/use-toast";
import Image from "next/image";
import { PlaceHolderImages } from "@/lib/placeholder-images";

export default function EbookEditPage({ params }: { params: { id: string } }) {
  const { toast } = useToast();
  const [ebook, setEbook] = useState<Ebook | null>(null);
  const [title, setTitle] = useState("");
  const [author, setAuthor] = useState("");
  const [price, setPrice] = useState(0);
  const [description, setDescription] = useState("");
  const [isLoading, setIsLoading] = useState(false);

  useEffect(() => {
    const ebookId = parseInt(params.id, 10);
    const foundEbook = ebooks.find((e) => e.id === ebookId);
    if (foundEbook) {
      setEbook(foundEbook);
      setTitle(foundEbook.title);
      setAuthor(foundEbook.author);
      setPrice(foundEbook.price);
      setDescription(foundEbook.description);
    }
  }, [params.id]);

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    setIsLoading(true);

    // In a real application, you would make an API call here
    // to update the ebook data in your database.
    console.log("Updated Ebook Data:", {
        id: ebook?.id,
        title,
        author,
        price,
        description
    });

    setTimeout(() => {
        toast({
            title: "Ebook Updated",
            description: `"${title}" has been successfully updated.`,
        });
        setIsLoading(false);
    }, 1000);
  };
  
  if (!ebook) {
    return <div className="flex items-center justify-center h-64">
        <Loader2 className="h-8 w-8 animate-spin text-primary" />
    </div>;
  }

  const image = PlaceHolderImages.find((img) => img.id === ebook.coverImageId);


  return (
    <div className="mx-auto grid max-w-[59rem] flex-1 auto-rows-max gap-4">
      <div className="flex items-center gap-4">
        <Button variant="outline" size="icon" className="h-7 w-7" asChild>
          <Link href="/admin">
            <ChevronLeft className="h-4 w-4" />
            <span className="sr-only">Back</span>
          </Link>
        </Button>
        <h1 className="flex-1 shrink-0 whitespace-nowrap text-xl font-semibold tracking-tight sm:grow-0">
          Edit: {ebook.title}
        </h1>
        <Badge variant="outline" className="ml-auto sm:ml-0">
          In Stock
        </Badge>
        <div className="hidden items-center gap-2 md:ml-auto md:flex">
          <Button variant="outline" size="sm">
            Discard
          </Button>
          <Button size="sm" onClick={handleSubmit} disabled={isLoading}>
            {isLoading && <Loader2 className="mr-2 h-4 w-4 animate-spin" />}
            Save Changes
          </Button>
        </div>
      </div>
      <form onSubmit={handleSubmit} className="grid gap-4 md:grid-cols-[1fr_250px] lg:grid-cols-3 lg:gap-8">
        <div className="grid auto-rows-max items-start gap-4 lg:col-span-2 lg:gap-8">
          <Card>
            <CardHeader>
              <CardTitle>Ebook Details</CardTitle>
              <CardDescription>
                Update the title, author, description, and price of the ebook.
              </CardDescription>
            </CardHeader>
            <CardContent>
              <div className="grid gap-6">
                <div className="grid gap-3">
                  <Label htmlFor="title">Title</Label>
                  <Input
                    id="title"
                    type="text"
                    className="w-full"
                    value={title}
                    onChange={(e) => setTitle(e.target.value)}
                  />
                </div>
                 <div className="grid gap-3">
                  <Label htmlFor="author">Author</Label>
                  <Input
                    id="author"
                    type="text"
                    className="w-full"
                    value={author}
                    onChange={(e) => setAuthor(e.target.value)}
                  />
                </div>
                <div className="grid gap-3">
                  <Label htmlFor="description">Description</Label>
                  <Textarea
                    id="description"
                    value={description}
                    onChange={(e) => setDescription(e.target.value)}
                    className="min-h-32"
                  />
                </div>
                <div className="grid gap-3">
                  <Label htmlFor="price">Price (GHâ‚µ)</Label>
                  <Input
                    id="price"
                    type="number"
                    value={price}
                    onChange={(e) => setPrice(parseFloat(e.target.value))}
                  />
                </div>
              </div>
            </CardContent>
          </Card>
        </div>
        <div className="grid auto-rows-max items-start gap-4 lg:gap-8">
          <Card>
            <CardHeader>
              <CardTitle>Cover Image</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="grid gap-2">
                <div className="relative aspect-[2/3] w-full rounded-md overflow-hidden border">
                    {image && (
                         <Image
                            alt="Product image"
                            className="object-cover"
                            fill
                            src={image.imageUrl}
                            data-ai-hint={image.imageHint}
                         />
                    )}
                </div>
                <div className="grid grid-cols-2 gap-2">
                  <Button variant="ghost">
                    <Upload className="h-4 w-4" />
                    <span className="ml-2">Upload</span>
                  </Button>
                </div>
              </div>
            </CardContent>
          </Card>
        </div>
      </form>
       <div className="flex items-center justify-center gap-2 md:hidden">
          <Button variant="outline" size="sm">
            Discard
          </Button>
          <Button size="sm" onClick={handleSubmit} disabled={isLoading}>
            {isLoading && <Loader2 className="mr-2 h-4 w-4 animate-spin" />}
            Save Changes
          </Button>
        </div>
    </div>
  );
}
